.app-header {
  color: #000;
  white-space: nowrap;
  text-align: center;
  font-size: 48px;
  border-bottom: 1px solid rgb(216, 216, 216);
}

.logo {
  letter-spacing: -3px;
  font-weight: 800;
  -webkit-transition: all 250ms cubic-bezier(0.4, 0.0, 0.2, 1);
  -o-transition: all 250ms cubic-bezier(0.4, 0.0, 0.2, 1);
  transition: all 250ms cubic-bezier(0.4, 0.0, 0.2, 1);
}


