.social-media-icons {
  width: 24px;
  height: 24px;
  padding-right: 10px;
}

.social-media-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /*padding-bottom: 5px;*/
}

.social-media-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.social-avatar {
  display: -ms-flexbox;
  display: flex;
}

.avatar-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-right: 20px;
}

.avatar-circle {
  border-radius: 50%;
}

@media only screen and (max-width: 736px) {
  .social-media-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  
  .social-media-wrapper {
    width: 50%;
  }

  .avatar-circle {
    width: 50px;
    height: 50px;
  }
}


