.right-side-bar {
  padding-left: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

#intro-message {
  margin-top: 0px;
  margin-bottom: 40px;
}

@media only screen and (max-width: 736px) {
  .right-side-bar {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
    -ms-flex-positive: initial;
        flex-grow: initial;
    -ms-flex-negative: 0;
        flex-shrink: 0;
  }

  #intro-message {
    margin-bottom: 20px;
  }
}


